export default class MockMonitor {
	constructor(bounds) {
		this.name = bounds.name;
		this.left = bounds.left;
		this.top = bounds.top;
		this.right = bounds.right;
		this.bottom = bounds.bottom;
		this._monitor = {
			availableRect: bounds,
			unclaimedRect: bounds
		};
	}
}



